import React from 'react';
import Web3 from "web3";
import {abi, contractAddress} from "../constants";
import Loading from "../loader";
import {
    Col,
    Card,
    Container,
    Row,
    CardBody,
    CardTitle,
    CardSubtitle,
    CardText,
    Button,
    FormGroup,
    Label, Input, Form
} from "reactstrap";
import Lottie from "lottie-react-web";
import pageNotFound from "../assets/page-not-found.json";
import {toast, ToastContainer} from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';
import ServiceHistoryList from "./components/service-history-list";
import moment from "moment";
import ServiceHistoryDetails from "./components/service-history-details";

class VehicleServiceView extends React.Component {
    state = {
        loading: true,
        loaded: false,
        chassisNumber: '',
    };

    web3;
    contract;

    componentDidMount() {
        this.web3 = new Web3(Web3.givenProvider || "http://localhost:8545");

        // "0x926387488Fbe109c47f85f38606ACb4008ce2633"

        this.web3.eth.getAccounts()
            .then((accounts) => {
                this.contract = new this.web3.eth.Contract(abi, contractAddress);
                this.contract.methods.getService().call({from: accounts[0]})
                    .then((service) => {
                        debugger;

                        console.log(service);

                        this.setState({
                            loading: false,
                            loaded: true,
                            serviceName: service[0],
                            serviceAddress: service[1],
                            walletAddress: service[2],
                            serviceExists: service[3],
                        });
                    });
            });
    }

    getCarInfo() {
        const {chassisNumber, walletAddress} = this.state;

        this.setState({
            loading: true
        }, () => {
            this.contract.methods.getCarAndServiceHistory(chassisNumber).call({from: walletAddress})
                .then((car) => {
                    if (car[1]) {
                        this.setState({
                            loading: false,
                            chassisNumber: car[0],
                            carExists: car[1],
                            serviceHistoryArrayServiceAddress: car[2],
                            serviceHistoryArrayServiceDate: car[3],
                            serviceHistoryCount: car[4]
                        });
                    } else {
                        this.setState({
                            loading: false,
                            carDoesNotExists: true,
                            carExists: false
                        }, () => toast('Chassis number does not exists!', {type: 'error'}));
                    }
                })
                .catch(() => {
                    this.setState({
                        loading: false,
                        carDoesNotExists: true,
                        carExists: false
                    }, () => toast('Chassis number does not exists!', {type: 'error'}));
                });
        });
    }

    registerCar() {
        const {walletAddress, chassisNumber} = this.state;

        this.setState({
            loading: true
        }, () => {
            this.contract.methods.registerCar(chassisNumber).send({from: walletAddress})
                .then((isCarCreated) => {
                    console.log("Car Registered!", isCarCreated);

                    this.setState({
                        loading: false,
                        carDoesNotExists: false
                    }, () => toast('Car Registered!', {type: 'success'}));
                    this.getCarInfo();
                })
                .catch((err) => {
                    console.log("Car Registered!", err);

                    this.setState({
                        loading: false
                    }, () => toast('Car already exists!', {type: 'error'}));
                });
        });
    }

    saveServiceHistoryAndOperation() {
        const {walletAddress, chassisNumber, serviceHistoryCount, vehicleOperations} = this.state;
        let now = moment().toString();

        let vehiclePartList = [];
        let operationDetailsList = [];
        for (const vehicleOperation of vehicleOperations) {
            vehiclePartList.push(vehicleOperation.vehiclePart);
            operationDetailsList.push(vehicleOperation.operationDetail);
        }

        this.setState({
            loading: true
        }, () => {
            this.contract.methods.addOperationsToServiceHistory(now, chassisNumber, serviceHistoryCount, vehiclePartList, operationDetailsList).send({from: walletAddress})
                .then((result) => {
                    console.log("addOperationsToServiceHistory Success!", result);

                    this.setState({
                        loading: false,
                        isCreatingNewServiceHistory: false,
                        renderServiceHistoryCreation: false
                    }, () => toast('Service History Created!', {type: 'success'}));
                    this.getCarInfo();
                })
                .catch((err) => {
                    console.log("addOperationsToServiceHistory Fail!", err);

                    this.setState({
                        loading: false,
                        isCreatingNewServiceHistory: false,
                    }, () => toast('Service History cannot be Created!', {type: 'error'}));
                });
        });
    }

    getOperationByServiceHistoryIndex(index) {
        const {walletAddress, chassisNumber} = this.state;

        this.setState({
            loading: true
        }, () => {
            this.contract.methods.getOperationsOfServiceHistory(chassisNumber, index).call({from: walletAddress})
                .then((result) => {
                    console.log("getOperationsOfServiceHistory Success!", result);

                    let vehiclePartsArray = result[0];
                    let detailsArray = result[1];

                    this.setState({
                        loading: false,
                        serviceHistoryDetailsVehiclePartsArray: vehiclePartsArray,
                        serviceHistoryDetailsDetailsArray: detailsArray,
                        selectedServiceHistoryIndex: index
                    });
                })
                .catch((err) => {
                    console.log("getOperationsOfServiceHistory Fail!", err);

                    this.setState({
                        loading: false,
                    }, () => toast('Cannot Fetch Service History Details!', {type: 'error'}));
                });
        });
    }

    renderCarDoesNotExists() {
        return (
            <Col>
                <div style={{
                    marginTop: 24,
                    marginBottom: 8
                }}>
                    Would you like to register this Chassis Number as a new car?
                </div>
                <Row style={{display: 'flex', justifyContent: 'flex-end'}}>
                    <Button color={"primary"} onClick={() => this.registerCar()}>Register</Button>
                </Row>
            </Col>
        );
    }

    renderServiceFound() {
        if (!this.state.loaded)
            return null;

        const {serviceName, serviceAddress, walletAddress} = this.state;

        return <Row>
            <Container>
                <Row style={{marginTop: 10}}>
                    <Col>
                        <Card>
                            <CardBody>
                                <CardTitle><h2>{serviceName}</h2></CardTitle>
                                <CardSubtitle>{serviceAddress}</CardSubtitle>
                                <CardText>{walletAddress}</CardText>
                            </CardBody>
                        </Card>
                    </Col>
                    <Col>
                        <Card>
                            <CardBody>
                                <Form>
                                    <FormGroup>
                                        <Label for="chasisNumber">Chassis Number</Label>
                                        <Input type="text"
                                               name="chasisNumber"
                                               id="chasisNumber"
                                               placeholder="1M8GDM9AXKP042788"
                                               value={this.state.chassisNumber}
                                               onChange={(e) => this.setState({
                                                   chassisNumber: e.target.value
                                               })}/>
                                    </FormGroup>
                                    <Row style={{display: 'flex', justifyContent: 'flex-end'}}>
                                        <Button color={"success"} onClick={() => this.getCarInfo()}>Get Car
                                            Info</Button>
                                    </Row>
                                </Form>
                                {this.state.carDoesNotExists && !this.state.carExists && this.renderCarDoesNotExists()}
                            </CardBody>
                        </Card>
                    </Col>
                </Row>

                {this.state.carExists &&
                <Row style={{marginTop: 20}}>
                    <Col sm={8}>
                        <Card>
                            <CardBody>
                                <CardTitle><h2>Service History</h2></CardTitle>
                                <ServiceHistoryList
                                    serviceHistoryArrayServiceAddress={this.state.serviceHistoryArrayServiceAddress}
                                    serviceHistoryArrayServiceDate={this.state.serviceHistoryArrayServiceDate}
                                    serviceHistoryCount={this.state.serviceHistoryCount}
                                    getOperationByServiceHistoryIndex={(index) => this.getOperationByServiceHistoryIndex(index)}
                                />
                                {!this.state.isCreatingNewServiceHistory &&
                                <Row style={{display: 'flex', justifyContent: 'flex-end', marginTop: 4}}>
                                    <Button color={"primary"}
                                            onClick={() => {
                                                this.setState({
                                                    renderServiceHistoryCreation: true,
                                                    isCreatingNewServiceHistory: true,
                                                    vehicleOperations: [{
                                                        vehiclePart: '',
                                                        operationDetail: ''
                                                    }]
                                                });
                                            }}>Create Service History</Button>
                                </Row>}
                            </CardBody>
                        </Card>
                    </Col>
                    {this.state.serviceHistoryDetailsVehiclePartsArray && <Col sm={4}>
                        <Card>
                            <CardBody>
                                <CardTitle><h2>Operations</h2></CardTitle>
                                <ServiceHistoryDetails
                                    selectedServiceHistoryIndex={this.state.selectedServiceHistoryIndex}
                                    vehiclePartsArray={this.state.serviceHistoryDetailsVehiclePartsArray}
                                    detailsArray={this.state.serviceHistoryDetailsDetailsArray}/>
                            </CardBody>
                        </Card>
                    </Col>}
                </Row>}

                {this.state.renderServiceHistoryCreation && this.renderServiceHistoryCreation()}
            </Container>
        </Row>

    }

    addNewOperation() {
        let {vehicleOperations} = this.state;
        let newOperation = {
            vehiclePart: '',
            operationDetail: ''
        };

        this.setState(prevState => ({
            vehicleOperations: [...prevState.vehicleOperations, newOperation]
        }));
    }

    renderServiceHistoryCreation() {
        if (!this.state.renderServiceHistoryCreation)
            return null;

        const {vehicleOperations} = this.state;
        let vehicleOperationsForms = vehicleOperations.map((item, index) => {
            let vehiclePartId = "vehiclePart" + index.toString();
            let operationDescriptionId = "operationDescription" + index.toString();

            return <Card
                style={{padding: 8, marginBottom: 16}}>
                <Form>
                    <FormGroup>
                        <Label for={vehiclePartId}>Vehicle Part</Label>
                        <Input type="text"
                               name={vehiclePartId}
                               id={vehiclePartId}
                               placeholder="Front Pillar"
                               value={vehicleOperations[index].vehiclePart}
                               onChange={(e) => {
                                   let {vehicleOperations} = this.state;
                                   vehicleOperations[index].vehiclePart = e.target.value;
                                   this.setState({
                                       vehicleOperations: vehicleOperations
                                   });
                               }}/>
                    </FormGroup>
                    <FormGroup>
                        <Label for={operationDescriptionId}>Operation Description</Label>
                        <Input type="text"
                               name={operationDescriptionId}
                               id={operationDescriptionId}
                               placeholder="Repairment"
                               value={vehicleOperations[index].operationDetail}
                               onChange={(e) => {
                                   let {vehicleOperations} = this.state;
                                   vehicleOperations[index].operationDetail = e.target.value;
                                   this.setState({
                                       vehicleOperations: vehicleOperations
                                   });
                               }}/>
                    </FormGroup>
                </Form>
            </Card>;
        });

        return (<Row style={{marginTop: 20, width: '50%'}}>
            <Col>
                <Card>
                    <CardBody>
                        <CardTitle><h2>Operations</h2></CardTitle>

                        {vehicleOperationsForms}

                        <div
                            style={{width: '100%', textAlign: 'center', fontSize: 12, color: 'blue', cursor: 'pointer'}}
                            onClick={() => this.addNewOperation()}>
                            Add New Operation
                        </div>

                        <Row style={{display: 'flex', justifyContent: 'flex-end', marginTop: 4}}>
                            <Button color={"primary"}
                                    onClick={() => this.saveServiceHistoryAndOperation()}>Save</Button>
                        </Row>
                    </CardBody>
                </Card>
            </Col>
        </Row>);
    }

    renderServiceNotFound() {
        if (!this.state.loaded)
            return null;

        return (
            <Col>
                <Lottie
                    options={{
                        animationData: pageNotFound,
                    }}
                    height={'50%'}
                    width={'50%'}
                    speed={1.5}
                />
                <div
                    style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                    Service information not found!
                </div>
            </Col>
        );
    }

    render() {
        const {serviceExists} = this.state;

        return <div style={{height: '100vh'}}>
            {serviceExists ? this.renderServiceFound() : this.renderServiceNotFound()}
            <ToastContainer
                className='toast-container'
                toastClassName="dark-toast"
                progressClassName={'toast-progress-style'}/>
            <Loading loading={this.state.loading}/>
        </div>;
    }
}

export default VehicleServiceView;