import React from 'react';
import "./App.css";
import LandingPageView from "./views/landing-page-view";
import {BrowserRouter as Router, Route} from 'react-router-dom';
import VehicleInformationView from "./views/vehicle-information-view";
import VehicleServiceView from "./views/vehicle-service-view";
import AdminView from "./views/admin-view";
import {ToastContainer} from "react-toastify";

const App = () => (
    <Router>
        <div>
            <Route exact path={'/be-auto/'} component={LandingPageView}/>
            <Route exact path={'/be-auto/vehicleInformation'} component={VehicleInformationView}/>
            <Route exact path={'/be-auto/vehicleService'} component={VehicleServiceView}/>
            <Route exact path={'/be-auto/admin'} component={AdminView}/>
        </div>
    </Router>
);

export default App;
