import React from "react";
import {Col, Pagination, PaginationItem, PaginationLink} from "reactstrap";
//import moment from "moment";
import ThisPlaceIsEmptyComponent from "./this-place-is-empty";

export default class PaginationList extends React.Component {
    state = {
        selectedPage: 1,
        itemList: []
    };
    numDocumentsInPage = 5;


    renderItems(list) {
    }

    render() {
        const {itemList} = this.state;
        if (!itemList || itemList.length === 0)
            return <ThisPlaceIsEmptyComponent/>;
        //itemList.sort((a, b) => moment(b.recordDate).isAfter(moment(a.recordDate)));
        let tempItemList = itemList.slice(
            (this.state.selectedPage - 1) * this.numDocumentsInPage,
            this.state.selectedPage * this.numDocumentsInPage);

        let numOfDocuments = itemList.length;
        let maxPageCount = Math.ceil(numOfDocuments / this.numDocumentsInPage);
        let foo = Array.from({length: maxPageCount}, (v, k) => k + 1);

        let paginationItemList = foo.map((item, index) => {
            let lowerBound = this.state.selectedPage - 2;
            let upperBound = this.state.selectedPage + 2;

            lowerBound = lowerBound < 1 ? 1 : lowerBound;
            upperBound = upperBound > maxPageCount ? maxPageCount : upperBound;

            if (lowerBound <= item && item <= upperBound) {
                return (<PaginationItem active={this.state.selectedPage === index + 1}>
                    <PaginationLink onClick={(_) => this.setState({
                        selectedPage: index + 1
                    })}>
                        {item}
                    </PaginationLink>
                </PaginationItem>)
            }
        });
        let renderedItemList = this.renderItems(tempItemList);
        return (
            <Col>
                <div>{renderedItemList}</div>
                <Pagination aria-label="Page navigation example" style={{marginTop: 10}}>
                    <PaginationItem>
                        <PaginationLink previous onClick={(_) => this.setState({
                            selectedPage: 1
                        })}/>
                    </PaginationItem>
                    {paginationItemList}
                    <PaginationItem>
                        <PaginationLink next onClick={(_) => this.setState({
                            selectedPage: Math.ceil(numOfDocuments / this.numDocumentsInPage)
                        })}/>
                    </PaginationItem>
                </Pagination>
            </Col>)
            ;
    }
}
