import React from 'react';
import {Row, Col, Button} from 'reactstrap';
import Lottie from 'lottie-react-web'
import "../App.css";
import serviceAnimation from "../assets/service.json";
import searchVehicleAnimation from "../assets/search-vehicle.json";
import Web3 from 'web3';
import {abi, contractAddress} from "../constants"

class LandingPageView extends React.Component {
    state = {};

    componentDidMount() {
        this.loadBlockChainData();
    }

    loadBlockChainData() {
        // Find the Provider
        const web3 = new Web3(Web3.givenProvider || "http://localhost:8545");

        // Get network type (Ropsten or smt. else)
        web3.eth.net.getNetworkType()
            .then((network) => {
                console.log("Network: ", network);
            });

        // Getting account address from metamask
        web3.eth.getAccounts()
            .then((accounts) => {
                console.log("Account: ", accounts[0]);
                this.setState({
                    userAddress: accounts[0]
                });
            });

        // Creating Contract and Calling a Function
        /*let contract = new web3.eth.Contract(abi, contractAddress);
        contract.methods.getHouseAddress().call()
            .then((result) => {
                console.log("getHouseAddress: ", result);
            });*/
    }

    render() {
        return (
            <div style={{height: '100vh'}}>
                <Row style={{height: '100vh'}}>
                    <Col xs="6">
                        <div style={{
                            height: '60%',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                        }}>
                            <Lottie
                                options={{
                                    animationData: searchVehicleAnimation,
                                }}
                                height={'50%'}
                                width={'50%'}
                                speed={0.5}
                            />
                        </div>
                        <div
                            style={{display: 'flex', justifyContent: 'center'}}>
                            <Button
                                color="primary"
                                onClick={() => this.props.history.push('/be-auto/vehicleInformation')}>
                                Car Looker
                            </Button>
                        </div>
                    </Col>

                    <Col xs="6">
                        <div style={{
                            height: '60%',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                        }}>
                            <Lottie
                                options={{
                                    animationData: serviceAnimation,
                                }}
                                height={'85%'}
                                width={'85%'}
                                speed={0.4}
                            />
                        </div>
                        <div
                            style={{display: 'flex', justifyContent: 'center'}}>
                            <Button
                                color="primary"
                                onClick={() => this.props.history.push('/be-auto/vehicleService')}>
                                Car Service
                            </Button>
                        </div>
                    </Col>
                </Row>
            </div>
        );
    }
}

export default LandingPageView;
