import React from "react";
import service from "../../assets/service.png";
import {GiCarBattery} from "react-icons/gi"

export default class ServiceHistoryDetails extends React.Component {
    state = {};

    constructor(props) {
        super(props);
        this.state.vehiclePartsArray = this.props.vehiclePartsArray;
        this.state.detailsArray = this.props.detailsArray;
        this.state.selectedServiceHistoryIndex = this.props.selectedServiceHistoryIndex;

    }

    shouldComponentUpdate(nextProps, nextState, nextContext) {
        if (this.state.selectedServiceHistoryIndex !== nextProps.selectedServiceHistoryIndex) {
            this.setState({
                vehiclePartsArray: nextProps.vehiclePartsArray,
                detailsArray: nextProps.detailsArray,
                selectedServiceHistoryIndex: nextProps.selectedServiceHistoryIndex
            });
            return true;
        }

        return false;
    }

    render() {
        const {vehiclePartsArray, detailsArray} = this.state;
        if (detailsArray.length === 0)
            return null;

        let operations = [];
        for (let i = 0; i < detailsArray.length; i++) {
            let vehiclePart = vehiclePartsArray[i];
            let detail = detailsArray[i];

            operations.push(<div style={{
                flexDirection: 'row',
                marginBottom: 16,
                display: 'flex',
                borderRadius: 10,
                borderStyle: 'solid',
                alignItems: 'center',
                cursor: 'pointer',
                borderWidth: 1, borderColor: '#eee'
            }}>
                <GiCarBattery size={32} style={{marginLeft: 8}}/>
                <div style={{flexDirection: 'column', marginLeft: 8}}>
                    <div><h5>Vehicle Part</h5></div>
                    <div>{vehiclePart}</div>

                    <div style={{marginTop: 16}}><h5>Operation Detail</h5></div>
                    <div>{detail}</div>
                </div>
            </div>)
        }

        return <div>
            {operations}
        </div>
    }
}