import React from 'react';
import Web3 from "web3";
import {abi, contractAddress} from "../constants";
import Loading from "../loader";
import notAllowedAnimation from "../assets/not-allowed.json";
import Lottie from "lottie-react-web";
import {Col, Row, Button, Form, FormGroup, Label, Input} from 'reactstrap';
import {toast, ToastContainer} from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';


class AdminView extends React.Component {
    state = {
        loading: true,
        loaded: false,
        serviceName: '',
        serviceAddress: '',
        walletAddress: ''
    };

    web3;
    contract;

    componentDidMount() {
        this.web3 = new Web3(Web3.givenProvider || "http://localhost:8545");

        this.web3.eth.getAccounts()
            .then((accounts) => {
                console.log("Account: ", accounts[0]);

                // Creating Contract and Calling a Function
                this.contract = new this.web3.eth.Contract(abi, contractAddress);
                this.contract.methods.isHouseAddress(accounts[0]).call()
                    .then((result) => {
                        console.log("isHouseAddress: ", result);

                        this.setState({
                            loading: false,
                            loaded: true,
                            canSeeAdminPage: result,
                            userWalletAddress: accounts[0]
                        });
                    });
            });
    }

    registerService() {
        const {serviceName, serviceAddress, walletAddress, userWalletAddress} = this.state;
        if (serviceName.trim() === '' || serviceAddress.trim() === '' || walletAddress.trim() === '') {
            alert("Missing value!");
            return;
        }

        this.setState({
            loading: true,
        }, () => {
            this.contract.methods.registerService(walletAddress, serviceName, serviceAddress).send({from: userWalletAddress})
                .then((isServiceCreated) => {
                    console.log("Service Created!!!", isServiceCreated);

                    this.setState({
                        loading: false
                    }, () => toast('Service created!', {type: 'success'}));
                })
                .catch((err) => {
                    console.log("Service could not be Created!!!", err);

                    this.setState({
                        loading: false
                    }, () => toast('Service could not be created!', {type: 'error'}));
                })
        });
    }

    renderAdminPage() {
        if (!this.state.loaded)
            return null;

        return (
            <div style={{height: '100vh', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                <Col md={4}>
                    <Form>
                        <FormGroup>
                            <Label for="exampleAddress">Service Name</Label>
                            <Input type="text"
                                   name="address"
                                   id="exampleAddress"
                                   placeholder="Opel Deer"
                                   value={this.state.serviceName}
                                   onChange={(e) => this.setState({
                                       serviceName: e.target.value
                                   })}/>
                        </FormGroup>
                        <FormGroup>
                            <Label for="exampleAddress2">Physical Address</Label>
                            <Input type="text"
                                   name="address2"
                                   id="exampleAddress2"
                                   placeholder="Girne, Drtler sokagi No:46, 34852 Maltepe/stanbul"
                                   value={this.state.serviceAddress}
                                   onChange={(e) => this.setState({
                                       serviceAddress: e.target.value
                                   })}/>
                        </FormGroup>
                        <FormGroup>
                            <Label for="walletAddress">Wallet Address</Label>
                            <Input type="text"
                                   name="wallet"
                                   id="walletAddress"
                                   placeholder="0xB60EA5Af63d41D3B2bd127f987C461821b62d8ca"
                                   value={this.state.walletAddress}
                                   onChange={(e) => this.setState({
                                       walletAddress: e.target.value
                                   })}/>
                        </FormGroup>
                        <Button color={"success"} onClick={() => this.registerService()}>Register</Button>
                    </Form>
                </Col>
            </div>
        );
    }

    renderNotAdminPage() {
        if (!this.state.loaded)
            return null;

        return (
            <Col>
                <Lottie
                    options={{
                        animationData: notAllowedAnimation,
                    }}
                    height={'25%'}
                    width={'25%'}
                />
                <div
                    style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                    You are not allowed to view this page!
                </div>
            </Col>
        );
    }

    render() {
        const {canSeeAdminPage} = this.state;

        return (
            <div style={{
                height: '100vh'
            }}>
                {canSeeAdminPage ? this.renderAdminPage() : this.renderNotAdminPage()}
                <ToastContainer
                    className='toast-container'
                    toastClassName="dark-toast"
                    progressClassName={'toast-progress-style'}/>
                <Loading loading={this.state.loading}/>
            </div>
        );
    }
}

export default AdminView;