import React from 'react';
import PaginationList from "./pagination-list";
import moment from "moment";
import service from '../../assets/service.png'; // Tell webpack this JS file uses this image


export default class ServiceHistoryList extends PaginationList {

    componentDidMount() {
        let serviceHistoryArray = [];
        let serviceHistoryArrayServiceDate = this.props.serviceHistoryArrayServiceDate;
        let serviceHistoryArrayServiceAddress = this.props.serviceHistoryArrayServiceAddress;
        for (let i = 0; i < this.props.serviceHistoryCount; i++) {
            serviceHistoryArray.push({
                serviceDate: serviceHistoryArrayServiceDate[i],
                serviceAddress: serviceHistoryArrayServiceAddress[i],
                serviceHistoryIndex: i
            });
        }

        this.setState({
            itemList: serviceHistoryArray,
            serviceHistoryCount: this.props.serviceHistoryCount
        });
    }

    shouldComponentUpdate(nextProps, nextState, nextContext) {
        if (this.state.serviceHistoryCount !== nextProps.serviceHistoryCount) {
            let serviceHistoryArray = [];
            let serviceHistoryArrayServiceDate = nextProps.serviceHistoryArrayServiceDate;
            let serviceHistoryArrayServiceAddress = nextProps.serviceHistoryArrayServiceAddress;
            for (let i = 0; i < nextProps.serviceHistoryCount; i++) {
                serviceHistoryArray.push({
                    serviceDate: serviceHistoryArrayServiceDate[i],
                    serviceAddress: serviceHistoryArrayServiceAddress[i],
                    serviceHistoryIndex: i
                });
            }

            this.setState({
                itemList: serviceHistoryArray,
                serviceHistoryCount: nextProps.serviceHistoryCount
            });

            return true;
        }

        return false;
    }

    renderItems(itemList) {
        return itemList.map((item) => {
            return <a style={{cursor: 'pointer'}}
                      onClick={() => this.props.getOperationByServiceHistoryIndex(item.serviceHistoryIndex)}>
                <div style={{
                    marginBottom: 16,
                    flexDirection: 'row',
                    display: 'flex',
                    borderRadius: 10,
                    borderStyle: 'solid',
                    alignItems: 'center',
                    cursor: 'pointer',
                    borderWidth: 1, borderColor: '#eee'
                }}>
                    <img src={service} style={{height: 60}} alt="Logo"/>
                    <div style={{flexDirection: 'column', marginLeft: 4}}>
                        <div><h5>Service Wallet Address</h5></div>
                        <div>{item.serviceAddress}</div>

                        <div style={{marginTop: 16}}><h5>Service Visit Date</h5></div>
                        <div>{item.serviceDate}</div>
                    </div>
                </div>
            </a>
        });
    }
}
